export default function UserBlock({id} : {id : string}) {
  const { allMessages, setAllMessages , userPrompt} = useContext(userContext)!;

  // async function promptResponseFromAi(postBody : ResponseForTemplate) {
  //   try {
  //     const response = await axiosInstance.post("/take-prompt", postBody);
  //     const correctedXML = xmlToString(response.data);
  //     console.log(correctedXML);
  //     let dummyMessages = [...allMessages!];
  //     dummyMessages[dummyMessages?.length - 1].ai = correctedXML!;
  //     // allMessages = dummyMessages;
  //     console.log(dummyMessages);
  //     setAllMessages(dummyMessages);
  //   } catch (err) {
  //     toast.error("Ai Model is not Running");
  //   }
  // }

  useEffect(() => {
    async function getwebtateAndMessages() {
     const messages = await getMessages(id);
    //  console.log(messages)
     if(messages.data != null) {
      let allMessagesFromResponse = [];
        for(let i = 0; i < messages.data.messages.length; i++) {
          const newMessage = {
            user : messages.data.messages[i].userMessage,
            ai : JSON.parse(messages.data.messages[i].userMessage)
          }
          allMessagesFromResponse.push(newMessage)
        }

        console.log("converted Messages :",allMessagesFromResponse)
       setAllMessages(allMessagesFromResponse)
     }
    }

    getwebtateAndMessages()
  }, [allMessages]);
  

  return (
    <div>
      <Card>
        <CardContent>
          {allMessages
            ? allMessages.map((item, index) => (
                <div className="flex flex-col gap-1" key={index}>
                  <div className="flex justify-end">
                    <span>{item.user}</span>
                  </div>

                  <div className="flex justify-start">
                    {item.ai.map((newItem, index) => (
                      <Card key={index}>
                        <CardContent className="flex gap-3">
                          {newItem.status == "Pending" ? (
                            <TicketCheckIcon />
                          ) : (
                            <ListChecksIcon />
                          )}
                          {newItem.title}
                        </CardContent>
                      </Card>
                    ))}
                  </div>
                </div>
              ))
            : ""}
        </CardContent>
      </Card>
    </div>
  );
}
